export const getImageUrl = (path: string | null | undefined): string => {
  if (!path) return '';

  if (path.startsWith('http://') || path.startsWith('https://')) {
    return path;
  }

  if (path.startsWith('/')) {
    const GCS_BASE_URL = 'https://storage.googleapis.com/miniblog-storage';
    return `${GCS_BASE_URL}${path}`;
  }

  // 3. ÏòàÏ†Ñ Î∞©Ïãù(Î°úÏª¨ ÏÑúÎ≤Ñ) ÎåÄÏùëÏù¥ ÌïÑÏöî ÏóÜÎã§Î©¥ ÏúÑ Îã®Í≥ÑÏóêÏÑú ÎßàÎ¨¥Î¶¨Îê©ÎãàÎã§.
  // ÌïÑÏöîÌïú Í≤ΩÏö∞ÏóêÎßå ÏïÑÎûò Ï£ºÏÜåÎ•º Ïú†ÏßÄÌïòÏÑ∏Ïöî.
  const USERSERVICE_URL = 'http://127.0.0.1:1003';
  return `${USERSERVICE_URL}${path}`;
};

export const getInitial = (userId: string | null | undefined): string => {
  if (!userId || userId.length === 0) return '?';
  return userId[0].toUpperCase();
};