// pages/settings/index.tsx

'use client';

import React, { useState, useEffect, useRef } from 'react';
import Layout from '@/component/layout/MainLayout'; // Í≤ΩÎ°úÏóê ÎßûÍ≤å ÏàòÏ†ï
import styles from '@/styles/userService/settings.module.css'; // CSS Î™®Îìà ÏÇ¨Ïö©
// üü¢ [Í∞ÄÏ†ï] updateNickname Ìï®ÏàòÍ∞Ä updateNicknameWithoutPasswordÎ°ú Î≥ÄÍ≤ΩÎêòÍ±∞ÎÇò,
// Í∏∞Ï°¥ Ìï®ÏàòÍ∞Ä ÎπÑÎ∞ÄÎ≤àÌò∏ ÏóÜÏù¥ Ìò∏Ï∂úÎê† Ïàò ÏûàÎèÑÎ°ù Î∞±ÏóîÎìú APIÎèÑ ÏàòÏ†ïÎêòÏñ¥Ïïº Ìï©ÎãàÎã§.
import { getUserProfile, UserProfile, uploadProfileImage, changePassword, updateNickname } from '@/api/userService/user';
import { getImageUrl, getInitial } from '@/utils/imageUtils';

const SETTINGS_TABS = {
  PROFILE: 'profile',
  PASSWORD: 'password',
} as const;

type ActiveTab = typeof SETTINGS_TABS[keyof typeof SETTINGS_TABS];

// === 1. ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ Î∞è Ï†ïÎ≥¥ ÏàòÏ†ï Ïª¥Ìè¨ÎÑåÌä∏ ===
interface ProfileSettingsProps {
  initialProfile: UserProfile;
  onProfileUpdate: (newProfile: UserProfile) => void;
}

const ProfileSettings: React.FC<ProfileSettingsProps> = ({ initialProfile, onProfileUpdate }) => {
  const [profile, setProfile] = useState<UserProfile>(initialProfile);
  const [isUpdating, setIsUpdating] = useState(false); // Ïù¥ÎØ∏ÏßÄ/ÎãâÎÑ§ÏûÑ ÏóÖÎç∞Ïù¥Ìä∏ Í≥µÏö© Î°úÎî© ÏÉÅÌÉú
  const fileInputRef = useRef<HTMLInputElement>(null);

  const [nickName, setNickName] = useState(initialProfile.nickName || '');
  const [email, setEmail] = useState(initialProfile.email || '');

  const [nicknameError, setNicknameError] = useState('');

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // ‚úÖ [ÏàòÏ†ï] Ï†úÌïú ÌÅ¨Í∏∞Î•º 5MBÎ°ú Î≥ÄÍ≤Ω (5 * 1024 * 1024)
    const MAX_SIZE = 5 * 1024 * 1024;
    if (file.size > MAX_SIZE) {
      alert("ÌååÏùº ÌÅ¨Í∏∞Îäî ÏµúÎåÄ 5MBÍπåÏßÄÎßå ÌóàÏö©Îê©ÎãàÎã§.");
      // ÌååÏùº ÏÑ†ÌÉù Ï¥àÍ∏∞Ìôî (Í∞ôÏùÄ ÌååÏùºÏùÑ Îã§Ïãú ÏÑ†ÌÉùÌï† Ïàò ÏûàÍ≤å Ìï®)
      e.target.value = '';
      return;
    }

    setIsUpdating(true);
    setNicknameError('');

    try {
      const { profileImg: newImgPath } = await uploadProfileImage(profile.userSignId, file);

      const updatedProfile: UserProfile = { ...profile, profileImg: newImgPath };

      setProfile(updatedProfile);
      onProfileUpdate(updatedProfile);

      // ‚úÖ [Ï∂îÍ∞Ä] TopbarÏôÄ ÎèôÍ∏∞ÌôîÎ•º ÏúÑÌï¥ localStorageÎèÑ Ìï®Íªò ÏóÖÎç∞Ïù¥Ìä∏
      localStorage.setItem('profile_img', newImgPath);

      // Ïï± Ï†ÑÏ≤¥Ïóê ÌîÑÎ°úÌïÑÏù¥ Î≥ÄÍ≤ΩÎêòÏóàÏùåÏùÑ ÏïåÎ¶¨Îäî Ïù¥Î≤§Ìä∏ Î∞úÏã†
      window.dispatchEvent(new Event('profileUpdate'));
      alert("ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.");

    } catch (error: any) {
      // ‚úÖ [ÏàòÏ†ï] Îü∞ÌÉÄÏûÑ ÏóêÎü¨ ÌéòÏù¥ÏßÄ Î∞©ÏßÄÎ•º ÏúÑÌï¥ catch Î°úÏßÅ Í∞ïÌôî
      console.error("ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®:", error);

      // ÏÑúÎ≤ÑÏóêÏÑú Ï†ÑÎã¨Ìïú Íµ¨Ï≤¥Ï†ÅÏù∏ ÏóêÎü¨ Î©îÏãúÏßÄÍ∞Ä ÏûàÎã§Î©¥ alertÎ°ú ÌëúÏãú
      const errorMessage = error.message || "ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ ÏóÖÎç∞Ïù¥Ìä∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.";
      alert(errorMessage);

      // ÏóêÎü¨ Î∞úÏÉù Ïãú ÌååÏùº ÏÑ†ÌÉù Ï¥àÍ∏∞Ìôî
      e.target.value = '';
    } finally {
      setIsUpdating(false);
    }
  };

  // üü¢ [ÏàòÏ†ï] ÏùºÎ∞ò Ï†ïÎ≥¥ (ÎãâÎÑ§ÏûÑ) ÏàòÏ†ï Ï≤òÎ¶¨
  const handleProfileSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setNicknameError('');

    const trimmedNickName = nickName.trim();
    const isNicknameChanged = trimmedNickName !== profile.nickName;

    if (!isNicknameChanged) {
      setNicknameError("ÎãâÎÑ§ÏûÑÏùÑ Î≥ÄÍ≤ΩÌï¥ÏïºÎßå ÏàòÏ†ïÌï† Ïàò ÏûàÏäµÎãàÎã§.");
      return;
    }

    // üî¥ [Ï†úÍ±∞] ÎπÑÎ∞ÄÎ≤àÌò∏ ÌïÑÏàò Í≤ÄÏÇ¨ Ï†úÍ±∞

    setIsUpdating(true);
    try {
      const response = await updateNickname(profile.userSignId, trimmedNickName); // ÎπÑÎ∞ÄÎ≤àÌò∏ ÌïÑÎìúÎ•º Îπà Î¨∏ÏûêÏó¥Î°ú ÎÑòÍπÄ

      const newNickname = response.nickname;

      const updatedProfile: UserProfile = { ...profile, nickName: newNickname };

      setProfile(updatedProfile);
      onProfileUpdate(updatedProfile);

      alert(`ÎãâÎÑ§ÏûÑÏù¥ Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.`);

    } catch (error) {
      const errMsg = error instanceof Error ? error.message : "ÎãâÎÑ§ÏûÑ Î≥ÄÍ≤Ω Ï§ë Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò Î∞úÏÉù";
      setNicknameError(errMsg);
    } finally {
      setIsUpdating(false);
    }
  };

  // üü¢ [ÏàòÏ†ï] Î≤ÑÌäº ÌôúÏÑ±Ìôî Ï°∞Í±¥: ÎãâÎÑ§ÏûÑÏù¥ Î≥ÄÍ≤ΩÎêòÏóàÍ∏∞Îßå ÌïòÎ©¥ Îê®
  const isNicknameUpdateReady = nickName.trim() !== profile.nickName;

  return (
      <div className={styles.profileSection}>
        <h2>ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥</h2>

        {/* 1. Ïù¥ÎØ∏ÏßÄ ÏàòÏ†ï ÏòÅÏó≠ */}
        <div className={styles.imageUploadArea}>
          <div className={styles.avatarWrapper} onClick={() => !isUpdating && fileInputRef.current?.click()}>
            {profile.profileImg ? (
                <img
                    src={getImageUrl(profile.profileImg)}
                    alt="ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ"
                    className={styles.avatar}
                />
            ) : (
                <div className={styles.avatarPlaceholder}>
                  {getInitial(profile.userSignId)}
                </div>
            )}
            <div className={styles.avatarOverlay}>
              {isUpdating ? 'ÏóÖÎ°úÎìú Ï§ë...' : 'Ïù¥ÎØ∏ÏßÄ Î≥ÄÍ≤Ω'}
            </div>
          </div>
          <input
              type="file"
              ref={fileInputRef}
              accept="image/*"
              onChange={handleFileChange}
              style={{ display: 'none' }}
              disabled={isUpdating}
          />
        </div>

        {/* 2. ÏùºÎ∞ò Ï†ïÎ≥¥ ÌëúÏãú/ÏàòÏ†ï ÏòÅÏó≠ */}
        <div className={styles.userInfoDisplay}>
          <p className={styles.userSignId}>ÏïÑÏù¥Îîî: @{profile.userSignId}</p>
          <form onSubmit={handleProfileSubmit} className={styles.profileForm}>

            {nicknameError && <p className={styles.errorMessage}>{nicknameError}</p>}

            {/* ÎãâÎÑ§ÏûÑ */}
            <label htmlFor="nickname">ÎãâÎÑ§ÏûÑ</label>
            <input
                id="nickname"
                type="text"
                value={nickName}
                onChange={(e) => {
                  setNickName(e.target.value);
                  setNicknameError(''); // ÏûÖÎ†• Ïãú Ïò§Î•ò Ï¥àÍ∏∞Ìôî
                }}
                required
                maxLength={20}
                disabled={isUpdating}
            />

            {/* Ïù¥Î©îÏùº */}
            <label htmlFor="email">Ïù¥Î©îÏùº</label>
            <input
                id="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled // Ïù¥Î©îÏùº Î≥ÄÍ≤ΩÏùÄ ÎπÑÌôúÏÑ±Ìôî Ïú†ÏßÄ
            />

            {/* üî¥ [Ï†úÍ±∞] ÎãâÎÑ§ÏûÑ Î≥ÄÍ≤ΩÏùÑ ÏúÑÌïú ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ ÏûÖÎ†• ÌïÑÎìú Ï†úÍ±∞ */}
            {/* <label htmlFor="currentPassword">ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ (ÎãâÎÑ§ÏûÑ Î≥ÄÍ≤Ω Ïãú ÌïÑÏàò)</label>
            <input
                id="currentPassword"
                type="password"
                value={currentPassword}
                onChange={(e) => {
                  setCurrentPassword(e.target.value);
                  setNicknameError('');
                }}
                disabled={isUpdating}
            />
            */}

            <p className={styles.note}>
              ÎãâÎÑ§ÏûÑ Î≥ÄÍ≤Ω Ïãú Î≥¥Ïïà Ï†àÏ∞®Í∞Ä .
              Ïù¥Î©îÏùºÏùÄ Î≥ÄÍ≤ΩÌï† Ïàò ÏóÜÏäµÎãàÎã§.
            </p>

            <button type="submit" disabled={isUpdating || !isNicknameUpdateReady}>
              {isUpdating ? 'ÏàòÏ†ï Ï§ë...' : 'ÎãâÎÑ§ÏûÑ ÏàòÏ†ï'}
            </button>
          </form>
        </div>
      </div>
  );
};


// === 2. ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïª¥Ìè¨ÎÑåÌä∏ (Î≥ÄÍ≤Ω ÏóÜÏùå) ===
const PasswordSettings: React.FC<{ userSignId: string }> = ({ userSignId }) => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [isChanging, setIsChanging] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // Í∏∞Î≥∏ Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
    if (!currentPassword || !newPassword || !confirmPassword) {
      setError("Î™®Îì† ÌïÑÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }
    if (newPassword.length < 8) {
      setError("ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Îäî 8Ïûê Ïù¥ÏÉÅÏù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.");
      return;
    }
    if (newPassword !== confirmPassword) {
      setError("ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ ÌôïÏù∏ ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
      return;
    }
    if (currentPassword === newPassword) {
      setError("ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÎèôÏùºÌï©ÎãàÎã§.");
      return;
    }

    setIsChanging(true);
    try {
      // API Ìò∏Ï∂ú: ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
      await changePassword(userSignId, currentPassword, newPassword);
      alert("ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§. Î≥¥ÏïàÏùÑ ÏúÑÌï¥ Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ïïº Ìï† Ïàò ÏûàÏäµÎãàÎã§.");

      // ÏÑ±Í≥µ ÌõÑ ÌïÑÎìú Ï¥àÍ∏∞Ìôî
      setCurrentPassword('');
      setNewPassword('');
      setConfirmPassword('');
    } catch (err) {
      const errMsg = err instanceof Error ? err.message : "ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ï§ë Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò Î∞úÏÉù";
      setError(errMsg);
    } finally {
      setIsChanging(false);
    }
  };

  return (
      <div className={styles.passwordSection}>
        <h2>ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω</h2>
        <form onSubmit={handleSubmit} className={styles.passwordForm}>
          {error && <p className={styles.errorMessage}>{error}</p>}

          <label>ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏</label>
          <input
              type="password"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              required
          />

          <label>ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ (8Ïûê Ïù¥ÏÉÅ, ÌäπÏàòÎ¨∏Ïûê Ìè¨Ìï®ÎèºÏïºÌï©ÎãàÎã§.)</label>
          <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              required
          />

          <label>ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</label>
          <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
          />

          <button type="submit" disabled={isChanging || !currentPassword || !newPassword || !confirmPassword}>
            {isChanging ? 'Î≥ÄÍ≤Ω Ï§ë...' : 'ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω'}
          </button>
        </form>
      </div>
  );
};


// === 3. Î©îÏù∏ ÏÑ§Ï†ï ÌéòÏù¥ÏßÄ Ïª¥Ìè¨ÎÑåÌä∏ (Î≥ÄÍ≤Ω ÏóÜÏùå) ===
export default function SettingsPage() {
  const [activeTab, setActiveTab] = useState<ActiveTab>(SETTINGS_TABS.PROFILE);
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [userId, setUserId] = useState<string>('');

  useEffect(() => {
    const id = localStorage.getItem('userSignId');
    if (id) {
      setUserId(id);

      const fetchProfile = async () => {
        try {
          const userProfile = await getUserProfile(id);
          if (userProfile) {
            setProfile(userProfile);
          }
        } catch (error) {
          console.error("ÌîÑÎ°úÌïÑ Î°úÎî© Ïã§Ìå®:", error);
          // Ïã§Ï†ú ÏÑúÎπÑÏä§ÏóêÏÑúÎäî ÏóêÎü¨ Ïãú ÏÇ¨Ïö©ÏûêÏóêÍ≤å ÏïåÎ¶º
        } finally {
          setIsLoading(false);
        }
      };

      fetchProfile();
    } else {
      // Î°úÍ∑∏Ïù∏ÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ (Ïã§Ï†úÎ°úÎäî Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏ ÌïÑÏöî)
      setIsLoading(false);
    }
  }, []);

  if (isLoading) {
    return (
        <Layout>
          <div className={styles.loading}>ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Î°úÎî© Ï§ë...</div>
        </Layout>
    );
  }

  if (!userId || !profile) {
    return (
        <Layout>
          <div className={styles.container}>Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.</div>
        </Layout>
    );
  }

  return (
      <Layout>
        <div className={styles.settingsPage}>
          <h1 className={styles.pageTitle}>Í≥ÑÏ†ï ÏÑ§Ï†ï</h1>

          <div className={styles.contentWrapper}>
            {/* Ï¢åÏ∏° ÌÉ≠ Î©îÎâ¥ */}
            <nav className={styles.sidebar}>
              <button
                  className={activeTab === SETTINGS_TABS.PROFILE ? styles.activeTab : ''}
                  onClick={() => setActiveTab(SETTINGS_TABS.PROFILE)}
              >
                ÌîÑÎ°úÌïÑ Í¥ÄÎ¶¨
              </button>
              <button
                  className={activeTab === SETTINGS_TABS.PASSWORD ? styles.activeTab : ''}
                  onClick={() => setActiveTab(SETTINGS_TABS.PASSWORD)}
              >
                ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
              </button>
            </nav>

            {/* Ïö∞Ï∏° ÏΩòÌÖêÏ∏† ÏòÅÏó≠ */}
            <main className={styles.mainContent}>
              {activeTab === SETTINGS_TABS.PROFILE && (
                  <ProfileSettings
                      initialProfile={profile}
                      onProfileUpdate={setProfile} // ÏóÖÎç∞Ïù¥Ìä∏Îêú ÌîÑÎ°úÌïÑÏùÑ Î©îÏù∏ Ïª¥Ìè¨ÎÑåÌä∏Ïóê Î∞òÏòÅ
                  />
              )}

              {activeTab === SETTINGS_TABS.PASSWORD && (
                  <PasswordSettings userSignId={userId} />
              )}
            </main>
          </div>
        </div>
      </Layout>
  );
}