import { USERSERVICE_API } from "@/config/env";
import {
  UserDTO,
  LoginDTO,
  FriendReqDTO,
  FriendsResDTO,
} from "@/types/userService/user";

// Í≥µÌÜµ ÏùëÎãµ ÌÉÄÏûÖ
interface ApiResponse {
  success: boolean;
  message?: string;
}

// Ï§ëÎ≥µ Ï≤¥ÌÅ¨ ÏùëÎãµ ÌÉÄÏûÖ (Î∞±ÏóîÎìúÏóêÏÑú "exists" ÎòêÎäî "not exists" Í∞ôÏùÄ Î¨∏ÏûêÏó¥ Î∞òÌôò ÏòàÏÉÅ)
interface DuplicateCheckResponse {
  available: boolean;   // ÌîÑÎ°†Ìä∏ÏóêÏÑú Ïì∞Í∏∞ Ï¢ãÍ≤å ÌÜµÏùº
}

export interface UserProfile {
  userSignId: string;
  userName: string;
  nickName: string;
  email: string;
  profileImg: string | null;
}

export async function uploadProfileImage(userSignId: string, imageFile: File): Promise<{ profileImg: string }> {
  const formData = new FormData();
  formData.append('file', imageFile);

  const response = await fetch(`${USERSERVICE_API}/profile/image`, {
    method: 'POST',
    headers: {
      // ÌååÏùº ÏóÖÎ°úÎìúÎäî Content-TypeÏùÑ 'multipart/form-data'Î°ú Î™ÖÏãúÌïòÏßÄ ÏïäÏïÑÏïº
      // Î∏åÎùºÏö∞Ï†ÄÍ∞Ä boundaryÎ•º ÏûêÎèôÏúºÎ°ú ÏÑ§Ï†ïÌï©ÎãàÎã§.
      userSignId: userSignId, // ÏÇ¨Ïö©Ïûê ÏãùÎ≥ÑÏùÑ ÏúÑÌïú Header
      // Authorization Ìó§ÎçîÍ∞Ä ÌïÑÏöîÌïòÎã§Î©¥ Ïó¨Í∏∞Ïóê Ï∂îÍ∞ÄÌï¥Ïïº Ìï©ÎãàÎã§.
    },
    body: formData,
  });
  if (!response.ok) {
    // ‚úÖ ÏÑúÎ≤ÑÏóêÏÑú 413(Payload Too Large) Îì±ÏùÑ Î≥¥ÎÉàÏùÑ Îïå Î©îÏãúÏßÄ Ï≤òÎ¶¨
    let errorMsg = "";
    try {
      errorMsg = await response.text();
    } catch (e) {
      errorMsg = "ÏÑúÎ≤Ñ ÏùëÎãµ ÌååÏã± Ïã§Ìå®";
    }

    if (response.status === 413) {
      throw new Error("ÏÑúÎ≤ÑÏóêÏÑú ÌóàÏö©ÌïòÎäî ÌååÏùº ÌÅ¨Í∏∞Î•º Ï¥àÍ≥ºÌñàÏäµÎãàÎã§ (ÏµúÎåÄ 5MB).");
    }

    throw new Error(errorMsg || "ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®");
  }

  return await response.json();
}

/**
 * [Ï∂îÍ∞Ä] ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
 * @param userSignId ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê ID
 * @param currentPassword ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏
 * @param newPassword ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏
 */
export async function changePassword(userSignId: string, currentPassword: string, newPassword: string): Promise<ApiResponse> {
  const response = await fetch(`${USERSERVICE_API}/profile/password`, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
      userSignId: userSignId, // ÏÇ¨Ïö©Ïûê ÏãùÎ≥ÑÏùÑ ÏúÑÌïú Header
    },
    body: JSON.stringify({
      currentPassword: currentPassword,
      newPassword: newPassword,
    }),
  });

  if (!response.ok) {
    const errorMsg = await response.text();
    throw new Error(errorMsg || "ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïã§Ìå®");
  }

  // ÏÑ±Í≥µ Ïãú Î©îÏãúÏßÄÎ•º Î∞òÌôòÌïúÎã§Í≥† Í∞ÄÏ†ï
  return { success: true, message: "ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§." };
}

/**
 * ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ Ï°∞Ìöå
 */
export async function getUserProfile(userSignId: string): Promise<UserProfile | null> {
  try {
    const response = await fetch(`${USERSERVICE_API}/profile/${userSignId}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      console.error(`ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ïã§Ìå®: ${response.status}`);
      return null;
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ Ï°∞Ìöå Ï§ë Ïò§Î•ò:', error);
    return null;
  }
}

/**
 * Ïó¨Îü¨ ÏÇ¨Ïö©ÏûêÏùò ÌîÑÎ°úÌïÑ Ï†ïÎ≥¥ ÏùºÍ¥Ñ Ï°∞Ìöå
 */
export async function getUserProfiles(userSignIds: string[]): Promise<Map<string, UserProfile>> {
  const profileMap = new Map<string, UserProfile>();

  // Ï§ëÎ≥µ Ï†úÍ±∞
  const uniqueIds = [...new Set(userSignIds)];

  // Î≥ëÎ†¨Î°ú Ï°∞Ìöå
  const results = await Promise.allSettled(
      uniqueIds.map(id => getUserProfile(id))
  );

  results.forEach((result, index) => {
    if (result.status === 'fulfilled' && result.value) {
      profileMap.set(uniqueIds[index], result.value);
    }
  });

  return profileMap;
}

// 1. ÌöåÏõêÍ∞ÄÏûÖ
export async function signup(formData: FormData): Promise<ApiResponse> {
  const response = await fetch(`${USERSERVICE_API}/signup`, {
    method: "POST",
    // FormData ÏÇ¨Ïö© Ïãú Content-Type Ìó§ÎçîÎ•º ÏÑ§Ï†ïÌïòÏßÄ ÏïäÏùå (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏûêÎèôÏúºÎ°ú ÏÑ§Ï†ï)
    body: formData,
  });

  const text = await response.text();

  if (response.status === 201) {
    return { success: true, message: text || "ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ" };
  }

  return { success: false, message: text || "ÌöåÏõêÍ∞ÄÏûÖ Ïã§Ìå®" };
}
// 2. Î°úÍ∑∏Ïù∏
export async function login(loginDto: LoginDTO): Promise<{
  userSignId: string;
  role: string;
  refreshToken: string;
  accessToken: string;
  profileImg?: string
}> {
  const response = await fetch(`${USERSERVICE_API}/login`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(loginDto),
  });

  if (!response.ok) {
    throw new Error(`Î°úÍ∑∏Ïù∏ Ïã§Ìå®: ${response.status}`);
  }

  const authHeader = response.headers.get("Authorization");
  const accessToken = authHeader?.startsWith("Bearer ") ? authHeader.substring(7) : "";
  const body = await response.json();

  return {
    userSignId: body.userSignId,
    role: body.role,
    refreshToken: body.refreshToken,
    accessToken,
    profileImg: body.profileImg,
  };
}

// 3. ÏïÑÏù¥Îîî Ï§ëÎ≥µ Ï≤¥ÌÅ¨ ‚Üí GET /existId?userId=xxx
export async function checkUserSignId(userSignId: string): Promise<DuplicateCheckResponse> {
  const response = await fetch(
      `${USERSERVICE_API}/existId?userId=${encodeURIComponent(userSignId)}`
  );

  const text = await response.text();
  const available = text.trim() === "not exists";
  return { available };
}

